import React, { useState, useEffect } from 'react'
import axios from 'axios'
import { Link } from 'react-router-dom'
import { Container } from 'react-bootstrap'

function Prem() {

  const [user, setuser] = useState([])
  useEffect(() => {
    fetchuser()
  }, [])

  const fetchuser = async (name) => {
    await axios.get(`https://api.github.com/users/premshetty`)
      .then((res => {
        setuser(res.data)

      })
      )
      .catch(err => console.log(err));
  }
console.log(user);
  return (
    <div>
      <div  className ={ 'container'} style={{ height: '100vh' }}>
        <img  src={user.avatar_url}     />
        <h1> NAME: {user.name} </h1>
        <h1> EMAIL: {user.email} </h1>
        <h1> COMPANY: {user.company} </h1>
        <h1> FOLLOWERS: {user.followers} </h1>
        <h1> FOLLOWING: {user.following} </h1>
        
      <Link to='/' className ={"pagination" }>
            <button style ={{ textAlign : 'center'}} > TABLE </button>
          </Link>
      </div>
      
    </div>
  )
}

export default Prem;
