import React , { useState ,useEffect} from 'react'
import axios from 'axios'

function Mydetails( ) {
    const [user, setuser] = useState([] )
   useEffect(() => {
    
    const name = ['premshetty' , 'andrew','michale', 'elon' , 'mark'];
    name.map( user=>  fetchuser( user)  )
   
       
   } ,[])
  
   const fetchuser= async(name ,user)=>{
      await  axios.get(`https://api.github.com/users/${name}`)
        .then((res => 
            {
               setuser(res.data)
             
            })
        )
          .catch(err => console.log(err));
    }
   
  
console.log( user );
    return (
        <tr>
        <td>{user.name}</td>
        <td><img src={ user.avatar_url } style ={{ width:'20px' , height:'20px'}} /></td>
        <td>{user.email}</td>
        <td>{user.company}</td>
        <td>{user.followers}</td>
        <td>{user.following}</td> 
    </tr>
    )
}

export default Mydetails
