import React from 'react'
import { Table } from 'react-bootstrap'
import { Link } from 'react-router-dom'
import Prem from './Prem'
import Mydetails from './Rows'
function UserTable() {
    return (
        <div>
            <Table className={'  bg-secondary  w-75  mt-5 p-5  mx-auto'} striped bordered hover size="sm">
                <thead>
                    <tr>
                        <th>name</th>
                        <th>image</th>
                        <th>email</th>
                        <th>company</th>
                        <th>followers</th>
                        <th>following</th>
                    </tr>
                </thead>
                <tbody>
                    <Mydetails />
                    <Mydetails />
                    <Mydetails />
                    <Mydetails />
                    <Mydetails />
                </tbody>
            </Table>

            <Link to='/prem'>
                <button > PREM </button>
            </Link>
        </div>
    )
}

export default UserTable
